import React,{useState, useEffect} from 'react';
import axios from 'axios';
import DefaultLayout from "../components/DefaultLayout";
import { Col, Row } from 'antd';
import ItemLists from '../components/ItemLists';
import { useDispatch } from 'react-redux';



const Homepage = () => {
    const [itemsData, setItemsData] = useState([])
    const [selectCategory, setSelectCategory] = useState('Soups')
    const categories = [
      {
        name : 'Soups',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQPsWoPMHFvPIkfcLBwd7fBT8Swh0F063w7gg&usqp=CAU'
      },
      {
        name : 'Starter',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQIraAWRVEB7AdCUOF5HfdHkrpcGPP0o62KZQ&usqp=CAU'
      },
      {
        name : 'Roti',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ2foG5gikWHMoV4ibSOftGNzOG0SsvbZxxgg&usqp=CAU'
      },
      {
        name : 'Continental',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTdVbnICu-ooEEHstWfs1f4KTFSNZuWCGS8lQ&usqp=CAU'
      },
      {
        name : 'Indian',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS_GT-AOfbIv0oz_n3LSGL77hr2EP9htMKkwQ&usqp=CAU'
      },
      {
        name : 'Dessert',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTe3ggHh1Qh_ftj0SZulg1RRNrnIk5T3H1r2Q&usqp=CAU'
      },
      {
        name : 'Rice',
        imageUrl : 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTkTdlwM6ZtfyXAndyZdGR-gQFGgtBA684o2g&usqp=CAU'
      },
    ]
    const dispatch = useDispatch()

    useEffect(() => {
        const getAllItems = async () => {
           try {
            dispatch({
              type : 'SHOW_LOADING'
            })
             const {data} = await axios.get('/api/items/get-item') 
             setItemsData(data);
            dispatch({type : 'HIDE_LOADING'})
             console.log(data)
           } catch (error) {
             console.log(error)
           }
        }
        getAllItems()
     },[dispatch])

  return (
   <DefaultLayout>
    <div className='d-flex'>
      {categories.map(category => (
        <div key={category.name} className={`d-flex category ${selectCategory === category.name && "category-active"}`}
        onClick={() => setSelectCategory(category.name)}
        >
           <h5>{category.name}</h5>
           <img src={category.imageUrl} alt={category.name} height='50px' width='50px'/>
        </div>
      ))}
    </div>
    <Row>
        {itemsData
          .filter((i) => i.category === selectCategory)
          .map((item) => (
            <Col xs={24} lg={6} md={12} sm={6}>
              <ItemLists key={item.id} item={item}></ItemLists>
            </Col>
          ))}
      </Row>
   </DefaultLayout>
  );
}

export default Homepage;
