import React,{useEffect, useState} from 'react';
import DefaultLayout from '../components/DefaultLayout';
import { useDispatch } from 'react-redux';
import axios from 'axios';
import {DeleteOutlined, EditOutlined } from '@ant-design/icons'
import { Button, Table, Modal, Form, Input, Select, message } from 'antd';

const ItemPage = () => {

  const dispatch = useDispatch();
  const [itemsData, setItemsData] = useState([])
  const [popModal,setPopModal] = useState(false)
  const [editItem, setEditItem] = useState(null)

 

  const getAllItems = async () => {
    try {
     dispatch({
       type : 'SHOW_LOADING'
     })
      const {data} = await axios.get('/api/items/get-item') 
      setItemsData(data);
     dispatch({type : 'HIDE_LOADING'})
      console.log(data)
    } catch (error) {
      dispatch({type : 'HIDE_LOADING'})
      console.log(error)
    }
 }

  //useeffect
  useEffect(() => {
    getAllItems()
 },[])

  

 //handle delete
 const handleDelete = async (record) => {
    try {
      dispatch({
        type : 'SHOW_LOADING'
      })
       await axios.post('/api/items/delete-item',{itemId : record._id}) 
       message.success('Item delted successfully')
       getAllItems()
       setPopModal(false)
      dispatch({type : 'HIDE_LOADING'})
     } catch (error) {
      dispatch({type : 'HIDE_LOADING'})
      message.error("Something went wrong")
       console.log(error)
     }
 }

 

 //table data
 const columns = [
  {title : 'Name', dataIndex : 'name'},
  {title : 'Image', dataIndex : 'image', render : (image,record) => <img src={image} alt={record.name} height='60' width='60'/> },
  {title : 'Price', dataIndex : 'price'},
  {title : 'Actions', dataIndex : '_id', 
  render : (id,record) =>
  <div>
     <DeleteOutlined 
     style={{cursor : 'pointer'}}
     onClick={() => {
      handleDelete(record)
     }}
     />
     <EditOutlined 
     style={{cursor : 'pointer'}} 
     onClick={() => {
      setEditItem(record);
      setPopModal(true)
     }}
     />
  </div>
},
]

//handle form submit
const handleSubmit = async (value) => {
  if(editItem === null){
    try {
      dispatch({
        type : 'SHOW_LOADING'
      })
       const res = await axios.post('/api/items/add-item',value) 
       message.success('Item added successfully')
       getAllItems()
       setPopModal(false)
      dispatch({type : 'HIDE_LOADING'})
     } catch (error) {
      dispatch({type : 'HIDE_LOADING'})
      message.error("Something went wrong")
       console.log(error)
     }
  }
  else{
    try {
      dispatch({
        type : 'SHOW_LOADING',
      })
       await axios.put('/api/items/edit-item',{...value, itemId : editItem._id}) 
       message.success('Item updated successfully')
       getAllItems()
       setPopModal(false)
      dispatch({type : 'HIDE_LOADING'})
     } catch (error) {
      message.error("Something went wrong")
       console.log(error)
     }
  }
}


  return (
    <DefaultLayout>
      <div className='d-flex justify-content-between'>
      <h1>Item List</h1>
      <Button type='primary' onClick={() => setPopModal(true)}>Add Item</Button>
      </div>
      <Table columns={columns} dataSource={itemsData} bordered/>
      {
        popModal && (
          <Modal title={`${editItem !== null ? 'Edit Item' : 'Add New Item'}`}
           visible={popModal} 
           onCancel={() => {
            setEditItem(null)
            setPopModal(false)
           }} 
           footer={false}>
        <Form layout='vertical' initialValues={editItem} onFinish={handleSubmit}>
          <Form.Item name='name' label='Name'>
            <Input/>
          </Form.Item>
          <Form.Item name='price' label='Price'>
            <Input/>
          </Form.Item>
          <Form.Item name='image' label='Image URL'>
            <Input/>
          </Form.Item>
          <Form.Item name='category' label='Category'>
          <Select>
            <Select.Option value='drinks'>Drinks</Select.Option>
            <Select.Option value='rice'>Rice</Select.Option>
            <Select.Option value='noodles'>Noodles</Select.Option>
          </Select>
          </Form.Item>
          <div className='d-flex justify-content-end'>
            <Button type='primary' htmlType='submit'>SAVE</Button>
          </div>
        </Form>
      </Modal>
        )
      }
    </DefaultLayout>
  );
}


export default ItemPage;
